@import '../../styles/partials/variables';

@import '../../styles/partials/fonts';

.addEvent {
    font-family: 'Roboto';

    &__main-container {
        @include desktop {
            display: flex;
        }
    }

    &__left-container {
        @include desktop {
            width: 50%;
        }
    }

    &__right-container {
        @include desktop {
            width: 50%;
        }
    }

    &__header {
        @include mobile {
            font-size: 16px;
            display: flex;
            justify-content: center;
            margin-left: 5vw;
            font-weight: normal;
            color: $font-color-3;
        }
        @include desktop {
            font-size: 25px;
            justify-content: flex-start;
            margin-left: 10vw;
            margin-top: 2rem;
        }
    }

    &__container {
        @include mobile {
            width: 280px;
            margin: auto;
            margin-top: 2rem;
            border-left: 1px solid $font-color-2;     
        }
        @include desktop {
            width: 400px;
            margin: 0;
            margin: 2rem 0 0 10vw;
        }
    }

    &__list {
        @include mobile {
            text-align: left;
            font-size: 13px;
        }
        @include desktop {
            font-size: 16px;
        }
    }

    &__list-item {
        @include mobile {
            list-style-type: none;
            padding-top: 0.5rem;
        }
    }

    &__button-container {
        @include mobile {
            display: flex;
            justify-content: center;
        }
        @include desktop {
            justify-content: flex-start;
            margin-left: 18vw;
        }
    }

    &__button {
        @include mobile {
            width: 100px;
            height: 30px;
            border: 1px solid $primary-color;
            font-family: 'Roboto';
            font-weight: bold;
            border-radius: 5px;
            font-size: 12px;
            cursor: pointer;
            -webkit-tap-highlight-color: transparent;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            z-index: 0;
            transition: 1s;
            margin-top: 2rem;
            outline:none;
          }
        }

        &__button::before,
        &__button::after {
            position: absolute;
            background: $font-color-3;
            width: 100px;
            height: 30px;
            border-radius: 5px;
            z-index: -1;
            transition: 1s;
            content: '';
        }

        &__button::before {
            width: 100px;
            height: 30px;
           
        }

        &__button::after {
            width: 100px;
            height: 30px;
        }

        &__add {
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
        }

        &__button:hover::before {
            width: 0px;
            background: $font-color-1;
        }

        &__button:hover::after {
            height: 0px;
            background: $font-color-1;
        }

        &__button:hover {
            background: $font-color-1;
        }

        &__image {
            @include mobile {
            width: 70%;
            margin-top: 2rem;
            }
            @include desktop {
                margin-top: -3rem;
            }
        }
    }